.Book {
  position: relative;
  border-radius: 10px;
  width: 15vw;
  height: 30vh;
  background-color: #CEFDFE;
  -webkit-box-shadow: 1px 1px 12px #000;
  box-shadow: 1px 1px 12px #000;
  -webkit-transform: preserve-3d;
  -ms-transform: preserve-3d;
  transform: preserve-3d;
  -webkit-perspective: 2000px;
  perspective: 600px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  color: #51a6a7;
  transition-duration: 0.5s;
  font-size: 1.5rem;
  z-index: 0;
}

.Cover,
.Page {
  padding: 1rem 1rem;
  top: 0;
  position: absolute;
  background-color: #CEFDFE;
  width: 100%;
  height: 100%;
  border-radius: 10px;
  cursor: pointer;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
  -webkit-transform-origin: 0;
  -ms-transform-origin: 0;
  transform-origin: 0;
  -webkit-box-shadow: 1px 1px 12px #000;
  box-shadow: 1px 1px 12px #000;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  flex-direction: column;
}

.Book:hover .Cover {
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
  -webkit-transform: rotatey(70deg);
  -ms-transform: rotatey(70deg);
  transform: rotatey(-70deg);
}

.Page {
  transform-origin: 100%;
}
.Book:hover .Page {
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
  -webkit-transform: rotateZ(25deg) rotateX(-30deg) rotateY(-10deg)
    translateX(140px);
  -ms-transform: rotateZ(25deg) rotateX(-30deg) rotateY(-10deg)
    translateX(140px);
  transform: rotateZ(25deg) rotateX(-30deg) rotateY(-10deg) translateX(140px);
  -webkit-box-shadow: 1px 1px 20px #000a;
  box-shadow: 1px 1px 20px #000a;
}

.Book:hover {
  transform: rotateZ(-10deg);
  z-index: 10;
}

.Book-Title {
  font-weight: bold;
  font-size: 1.2rem;
  text-align: center;
}

.Book-Link {
  margin-top: 1rem;
  text-decoration: none;
  color: #51a6a7;
  font-weight: bold;
  border: 1px solid #51a6a7;
  padding: 0.5rem 1rem;
  border-radius: 5px;
  transition-duration: 0.3s;
}